@use "@app/bancor-components/Container/BaseExternal";
@use "@app/bancor-components/Container/BaseInternal";
@use "@app/bancor-components/Element/Map";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Element/Markers/UserMarker";

$external-height: calc(Layout.get-height(beta) - Layout.get-size(kappa));
$internal-height: calc(Layout.get-height(beta) - BaseInternal.$size-header);

#{BaseExternal.$selector} {
    &.container {
        @include Margin.set($left: none, $right: none);
    }

    article>header {
        height: Layout.get-size(kappa);
    }

    article section>figure {
        @include Margin.set($bottom: none);
    }

    #{Map.$selector} {
        @include Position.set($position: relative);
        height: $external-height;
    }

    .buttons {
        top: Layout.get-space(lambda);
    }
}

#{BaseExternal.$selector},
.internalContainer {
    .buttons {
        @include Position.set($position: absolute);
        @include Display.set($display: flex);
        @include Position.set($z-index: iota, $right: none);
        @include Margin.set($right: alpha);
    }

    .button {
        @include Margin.set($left: alpha, $right: alpha);
    }
}

.internalContainer {

    #{Map.$selector} {
        height: $internal-height;
        @include Position.set($position: relative);
    }

    .buttons {
        top: Layout.get-space(kappa);
    }
}

.info {
    @include Position.set($position: absolute, $bottom: none);
    @include Padding.set(beta);
    width: Layout.get-max-width(alpha);
}

.actions {
    @include Display.set($display: flex);
    width: Layout.get-max-width(alpha);

    .searchInThisArea {
        @include Margin.set($left: auto, $right: auto);
    }

    .focus {
        @include Color.set($bg: "transparent");
        @include Border.set($style: default);
    }
}

#{UserMarker.$selector} {
    @include Position.set($right: auto);
}
