@use "." as this;
@use "@app/bancor-components/Element/Markers/Marker";
@use "@app/bancor-components/Style/Layout/Position";

// This is necessary to position the icon in the exact coordinates
$user-marker-position: -23px;

#{this.$selector} {
    #{Marker.$selector} {
        @include Position.set($top: $user-marker-position, $right: $user-marker-position);
    }
}
